import React, {useState} from 'react';
import logo from './logo.svg';
import './App.css';
import { Navbar,Nav, NavDropdown,Form,FormControl,Button } from 'react-bootstrap';
import { Jumbotron } from 'react-bootstrap';
import Data from './data.js';
import Detail from './Detail.js';
import axios from 'axios';

import { Link, Route, Switch } from 'react-router-dom';

function App() {
// data컴포넌트 이용
  let [추천,추천변경] = useState(Data);
  let [재고,재고변경] = useState([10,11,12]);

  return (
    <div className="App">
     <Navbar bg="light" expand="lg">
    <Navbar.Brand href="#home">POETREE</Navbar.Brand>
    <Navbar.Toggle aria-controls="basic-navbar-nav" />
    <Navbar.Collapse id="basic-navbar-nav">
      <Nav className="ml-auto">
        <Nav.Link > <Link to="/">Home</Link></Nav.Link>
        <Nav.Link > <Link to="/detail">Detail</Link></Nav.Link>
        <NavDropdown title="Dropdown" id="basic-nav-dropdown">
          <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
          <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
          <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
          <NavDropdown.Divider />
          <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
        </NavDropdown>
      </Nav>
      <Form inline>
        <FormControl type="text" placeholder="Search" className="mr-sm-2" />
        <Button variant="outline-success">Search</Button>
      </Form>
    </Navbar.Collapse>
  </Navbar>


    <Switch>
{/* 라우팅 */}
    <Route exact path="/">
      <Jumbotron className="background">
        <h1>나만의 시가 펼쳐지는 공간, poetree</h1>
        <p>
          오늘의 사진을 보고 느낀 감정과 생각을 시로 기록해보세요.
        </p>
        <p>
          <Button variant="primary">시 작성하기</Button>
        </p>
      </Jumbotron>

      {/* 시 리스트 레이아웃 */}
      <div className="container">
        {/* 12컬럼으로 쪼개 */}
        <div className="row">
          {
            추천.map(function(데이터,i){
              return(
                // props 사용할 것임
                <List 추천={추천[i]} i={i}></List>
              )
            })
          }
        </div>
        
        <Button onClick={()=>{

          // 서버에 get요청
          axios.get('https://codingapple1.github.io/shop/data2.json')
          .then((result)=>{
            console.log(result.data)
            추천변경([...추천,...result.data])
          })
          .catch(()=>{
            console.log('오류')
          })
        }}>더보기</Button>

      </div>
    </Route>
    
{/* 상세페이지 */}
    <Route path="/detail/:id">
      <Detail 추천={추천} 재고={재고} 재고변경={재고변경}/>
    </Route>
        
    <Route path="/:id">
      <div>아무거나 적었을 때 이거 보여주셈</div>
    </Route>

    </Switch>

    </div>
  );
}



function List(props){
  return(
    <div className="col-md-4">
          <img src={"https://codingapple1.github.io/shop/shoes"+(props.i + 1)+".jpg"} width="100%" />
          <h4>{props.추천.title}</h4>
          <p>{props.추천.content}</p>
        </div>
  )
}

export default App;
